<template>
  <div class="app common-layout">
    <el-container>
      <el-header class="header">
        <Header @toggle-sidebar="toggleCollapse"/>
      </el-header>
      <el-container>
        <Aside v-model:isCollapse="isAsideCollapsed"/>
        <el-main height="100%">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from './components/baseComponents/Header.vue'
import Aside from './components/baseComponents/Aside.vue'

export default {
  name: 'App',
  components: {
    Header,
    Aside,
  },
  data() {
    return {
      isAsideCollapsed: true
    }
  },
  
  methods: {
    toggleCollapse() {
      window.requestAnimationFrame(() => {
        this.isAsideCollapsed = !this.isAsideCollapsed;
      });
    }
  },
}
</script>

<style>
  * {
    font-family: 'Ubuntu', sans-serif;
  }
  .header{
    display: flex;
    align-items: center;
    min-height: 80px;
    border-bottom: 1px solid #EEF0F3;
  }
  
</style>
